import React from "react";
import { withRouter } from "next/router";
import AnimationLinks from "../components/AnimationLinks";
import MainLayout from "../components/MainLayout";
import { upperCaseLink } from "../js/utils";
export default withRouter(
  MainLayout(
    class Animation extends React.Component {
      componentDidMount() {
        this.mountPathArr = window.location.pathname.split("/");
        this.mountPathLength = this.mountPathArr.length;
        this.mountQuery = this.mountPathArr[this.mountPathLength - 1];
      }
      static async getInitialProps(ctx) {
        return { data: await ctx.query };
      }
      render() {
        const classCondition = () => {
          console.log(this.mountQuery);
          const pathArr = this.props.router.asPath.split("/");
          const pathLength = this.props.router.asPath.split("/").length;
          const query = pathArr[pathLength - 1];
          console.log(query);
          if (
            this.props.data.name === this.mountQuery &&
            this.props.data.name === query
          ) {
            return "cust__active col nav-link";
          }
          return "col nav-link";
        };
        let Animation;
        if (this.props.data.name) {
          const linkName = upperCaseLink(this.props.data.name);
          Animation = require("../components/animation/" + linkName).default;
        } else {
          Animation = () => {
            return <p>Animation Page</p>;
          };
        }

        return (
          <div>
            <AnimationLinks styleClass={classCondition(Animation)} />
            <Animation />
          </div>
        );
      }
    }
  )
);
